import uploadImg from "../../../components/uploadImg/uploadImg"
export default {
  components:{
    uploadImg
  },
  data() {
    return {
      showPopup:false,
      oid:'',
      tid:'',
      detail:'',
      type:'',
      columns:[],
      cancelRadio:'',
      formItem:{
        aftersales_type:'',
        evidence_pic:[],
        description:'',
        reason:'',
        oid:'',
        tid:''
      }
    };
  },
  onLoad:function(options){
    this.formItem.oid = options.oid
    this.formItem.tid = options.tid
    this.formItem.aftersales_type = options.type
    this.getSaleInfo()
  },
  methods: {
    submitSale(){
      if(this.formItem.reason===''){
        uni.showToast({
          title: '请选择售后原因',
          icon: 'none',
        })
        return
      }
      this.$http({
        aurl:'aftersales',
        data:this.formItem,
        success:res => {
          if(res.data.code===0){
            uni.showToast({
              title: '申请成功',
              icon: 'none',
            })
            setTimeout(function(){
              uni.navigateBack({
                delta:2
              });
            },500)
          }
        },
        fail:error => {
          console.log(error)
        }
      })
    },
    getImgLists(lists){
      this.formItem.evidence_pic = lists
    },
    getSaleInfo(){
      this.$http({
        aurl:'aftersalesApply',
        data:{
          oid:this.formItem.oid,
          tid:this.formItem.tid
        },
        success:res => {
          console.log(res)
          if(res.data.code===0){
            this.detail = res.data.result.orderInfo
            this.columns = res.data.result.reason
          }
        },
        fail:error => {
          console.log(error)
        }
      })
    },
    cancelChange(index){
      this.cancelRadio = String(index.detail)
      this.formItem.reason = this.columns[index.detail]
      this.showPopup = false
    },
    cancelClick(index){
      this.cancelRadio = String(index)
      this.formItem.reason = this.columns[index]
      this.showPopup = false
    },
    closeModel(){
      this.showPopup = false
    }
  },
  mounted() {
  },
};
